import React, { useState, useEffect } from 'react';
import { Alert, Container } from 'react-bootstrap';

import {
  Switch,
  Route,
  Redirect,
  useRouteMatch,
} from "react-router-dom";

import HomeForm from "./components/HomeForm";
import Fields from "./components/Fields";
import Field from "./components/Field";
import Contest from "./components/Contest";
import Problems from "./components/Problems";
import Problem from "./components/Problem";
import Submissions from "./components/Submission/Submissions";
import Ranking from "./components/Ranking/Ranking";
import Header from './components/Header/Header';
import LoginForm from './components/LoginForm';
import SignupForm from './components/SignupForm';

const Users = () => (
  <div>
    <h2>TKTL notes app</h2>
    <ul>
      <li>Matti Luukkainen</li>
      <li>Juha Tauriainen</li>
      <li>Arto Hellas</li>
    </ul>
  </div>
)

const App = () => {
  const [user, setUser] = useState({});
  const [message, setMessage] = useState(null);

  
  const matchField = useRouteMatch('/fields/:field');
  const fieldName = matchField ? matchField.params.field: null;
  
  const contestProblems = useRouteMatch("/contests/:contest/problems");
  let contestName = contestProblems ? contestProblems.params.contest: null;
  const contestSubmissions = useRouteMatch("/contests/:contest/submissions");
  if (!contestName) {
    contestName = contestSubmissions ? contestSubmissions.params.contest: null;
  }
  const contestRanking = useRouteMatch("/contests/:contest/ranking");
  if (!contestName) {
    contestName = contestRanking ? contestRanking.params.contest: null;
  }
  const matchContest = useRouteMatch('/contests/:contest');
  if (!contestName) {
    contestName = matchContest ? matchContest.params.contest: null;
  }

  const matchProblem = useRouteMatch('/problems/:problem');
  const problemName = matchProblem ? matchProblem.params.problem: null;

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedUser');
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON);
      setUser(user);
    }
  }, [])

  return (
    <div>
        {(message && message.startsWith("Success:") && 
          <Alert variant="success">
            {message}
          </Alert>
        )}
        {(message && message.startsWith("Error:") && 
          <Alert variant="danger">
            {message}
          </Alert>
        )}
      
      <Header user={user} />
      <Container>
        <Switch>
          <Route path="/fields/:field">
            <Field fieldName={fieldName} />
          </Route>
          <Route path="/contests/:contest/problems">
            <Problems contestName={contestName} />
          </Route>
          <Route path="/contests/:contest/submissions">
            <Submissions contestName={contestName} />
          </Route>
          <Route path="/contests/:contest/ranking">
            <Ranking contestName={contestName}/>
          </Route>
          <Route path="/contests/:contest">
            <Contest contestName={contestName} />
          </Route>
          <Route path="/problems/:problem">
            <Problem problemName={problemName} />
          </Route>
          <Route path="/fields">
            <Fields />
          </Route>
          <Route path="/users">
            {user ? <Users /> : <Redirect to="/login" />}
          </Route>
          <Route path="/login">
            <LoginForm setUser={(userData) => setUser(userData)} setMessage={(messageData) => setMessage(messageData)} />
          </Route>
          <Route path="/signup">
            <SignupForm setUser={(userData) => setUser(userData)} setMessage={(messageData) => setMessage(messageData)} />
          </Route>
          <Route path="/">
            <HomeForm />
          </Route>
        </Switch>
        <div>
          <br />
          <em>kuso app</em>
        </div>
      </Container>
    </div>
  )
}

export default App